/*==============================global==============================*/

html, body {
    max-width: 100%;
    overflow-x: hidden;
    background: #fff;
	height: 100%;
}

body {
	padding-top: 55px;
    color: #777;
	@media (max-width: 600px) {
		padding-top: 51px;
	}
}

$footerHeight: 25px;

body {
	color: #777;
	.main {
		min-height: 100%;
		padding-bottom: 20px;
		//margin: 20px;
		//margin-top: 0;
		//padding-top: $navHeight + 3px;
	}
	footer {
		display: flex;
		justify-content: space-around;
		padding: 11px;
		@include footer;
		height: $footerHeight;
		position: relative;
		margin-top: -$footerHeight;
		li {
			display: flex;
			align-items: center;
		}

	}
}

h1,h2,h3,h4,h5 {
	text-align: center;
}

p {
	// width: 238px;
	padding: 0 20px;
	text-align: center;
}

select {
	// font-size: 22px;
	padding-top: 4px;
    padding-right: 15px;
//    height: 32px;
    &.form-control {
        width: 170px;
    }
}

button {
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;
	color: #555;
	font-size: 14px;
	padding: 6px 12px;
	&:active {
		box-shadow: 0 3px 5px rgba(0, 0, 0, 0.125) inset;
		border: 1px solid black;
	}
	&:focus {
		outline: thin dotted;
		outline-offset: -2px;
	}
	&:hover, &:focus, &:active {
		background-color: #e6e6e6;
		border-color: #adadad;
		color: #333;
	}
}

input {
  // display: block;
  // width: 100%;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;
  color: #555;
  font-size: 14px;
//  height: 34px;
  line-height: 1.42857;
  padding: 6px 12px;
  &:focus {
    border-color: #66afe9;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
  }
}

input[type="checkbox"] {
	// height: 34px;
	// width: 34px;
	margin: 0;
}

a {
    cursor: pointer;
	color: $base4;
	&:hover, &:focus {
		color: $base4;
	}
}



th, table {
	td {
		vertical-align: middle !important;
	}
	text-align: center;
	@media (max-width: 600px) {
		font-size: 7px;
	}
}


caption {
	padding: 8px;
	font-weight: bold;
}

li {
	list-style-type: none;
}

h1 {
	@media (max-width: 600px) {
		font-size: 20px;
		margin: 2px 2px;
	}
}

